* {
  margin: 0;
  padding: 0;
}
.container {
  margin-left: auto;
  margin-right: auto;
  width: '100%';
  text-align: right;
  max-width: 1140px;
  background-color: #ffffff;
  font-family: 'Roboto', sans-serif;
}

.phone {
  width: 60px;
  height: 60px;
  position: fixed;
  right: 15px;
  bottom: 50px;
  z-index: 100;
  box-shadow: 12px 12px 28px rgba(0,0,0,0.7);
  background-color: rgba(46, 156, 46, 0.863);
  border-radius: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.phone img {
  width: 70%;
  height: 70%;
}

header {
  width: 100%;
  padding-bottom: 50px;
}

header h1 {
  font-size: 3.5rem;
  color: #ffffff;
  /* font-family: 'Roboto', sans-serif; */
  padding-top: 100px;
  margin-bottom: 50px;
  margin-right: 30px;
  text-shadow: -1px -1px white, 1px 1px gray, 2px 2px #7a7a7a, 3px 3px #757575, 4px 4px #707070, 5px 5px #6b6b6b, 6px 6px #666666, 7px 7px #616161, 8px 8px #5c5c5c, 9px 9px #575757, 10px 10px #525252, 11px 11px #4d4d4d, 18px 18px 30px rgba(0, 0, 0, .4), 18px 18px 10px rgba(0, 0, 0, .4);
}

header h3 {
  font-size: 1.6rem;
  color: #ffffff;
  /* font-family: 'Roboto', sans-serif; */
  margin-bottom: 50px;
  margin-right: 30px
}

h2 {
  font-size: 2.5rem;
  color: #000000;
  /* font-family: 'Roboto', sans-serif; */
  padding-top: 40px;
  padding-bottom: 40px;
}

.btn {
  padding: 5px 20px 5px 20px;
  font-size: 1.5em;
  /* font-family: 'Roboto', sans-serif; */
  background-color: dodgerblue;
  border-radius: 100px;
  display: inline-flex;
  color: white;
  text-decoration-line: none;
  align-items: center;
  justify-content: center;
  box-shadow: 12px 12px 28px rgba(0,0,0,0.7);
}
.btn:hover {
  background-color: rgb(89, 169, 250);
}

.easySchemeContainer {
  display: inline-flex;
  justify-content: flex-start;
  align-items: flex-start;
  max-width: 800px;
}

.easySchemeContainer ol {
  margin-left: 25px;
  text-align: left;
  counter-reset: li; 
  list-style: none; 
  font-size: 1.2rem;
  font-style: normal;
  line-height: 1.6;
  /* font-family: 'Roboto', sans-serif; */
  padding: 0;
  text-shadow: 0 1px 0 rgba(255,255,255,.5);
}

.easySchemeContainer li {
  position: relative;
  display: block;
  padding: .4em .4em .4em 2em;
  margin: .5em 0;
  background: #DAD2CA;
  color: #444;
  text-decoration: none;
  border-radius: .3em;
  transition: .3s ease-out;
}

.easySchemeContainer li::before {
  content: counter(li);
  counter-increment: li;
  position: absolute;
  left: -1.3em;
  top: 50%;
  margin-top: -1.3em;
  background: #8FD4C1;
  height: 2em;
  width: 2em;
  line-height: 2em;
  border: .3em solid white;
  text-align: center;
  font-weight: bold;
  border-radius: 2em;
  transition: all .3s ease-out;
}

.easySchemeContainer li:hover {
  background: #E9E4E0;
}
.easySchemeContainer li:hover:before {
  transform: rotate(360deg);
}

.section1 {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  background-color: dodgerblue;
  padding: 30px 20px 30px 20px;
}

.section1 p {
  max-width: 25%;
  text-align: left;
  padding: 0 20px 0 20px;
  /* font-family: 'Roboto', sans-serif; */
  font-size: 1.1em;
  font-style: normal;
  line-height: 1.6;
  color: #ffffff;
  letter-spacing: 0.5px;
}

.section1 p:hover {
  border-radius: 20px;
  background-color: rgb(74, 165, 255);
}

.section1:last-child {
  margin-right: 0px;
}

.section2 {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding-top: 30px;
  padding-bottom: 30px;
  font-size: 1.1rem;
}

.section2 > div {
  width: 47%;
}

.section2 ol {
  margin-left: 25px;
  text-align: left;
  counter-reset: li; 
  list-style: none; 
  font-size: 1.2rem;
  font-style: normal;
  line-height: 1.6;
  /* font-family: 'Roboto', sans-serif; */
  padding: 0;
  text-shadow: 0 1px 0 rgba(255,255,255,.5);
}

.section2 li {
  position: relative;
  display: block;
  padding: .4em .4em .4em 2em;
  margin: .5em 0;
  background: #DAD2CA;
  color: #444;
  text-decoration: none;
  border-radius: .3em;
  transition: .3s ease-out;
}

.section2 li::before {
  content: counter(li);
  counter-increment: li;
  position: absolute;
  left: -1.3em;
  top: 50%;
  margin-top: -1.3em;
  background: #8FD4C1;
  height: 2em;
  width: 2em;
  line-height: 2em;
  border: .3em solid white;
  text-align: center;
  font-weight: bold;
  border-radius: 2em;
  transition: all .3s ease-out;
}

.section2 li:hover {
  background: #E9E4E0;
}
.section2 li:hover:before {
  transform: rotate(360deg);
}

.section2 h3 {
  font-size: 1.8em;
  font-style: normal;
  line-height: 1.2;
  text-align: left;
  font-weight: 100;
  margin-bottom: 50px;
  margin-left: 40px;
}

.section2 a {
  font-size: 1.1em;
  height: 40px;
  color: #444;
  background-color: #DAD2CA;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.section2 a:hover {
  background: #E9E4E0;
  cursor: pointer;
}

#btn_container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 20px;
  margin-left: 20px;
}

.contact_us {
  padding: 50px 0 50px 0;
  background-color: dodgerblue;
  text-align: center;
}

.contact_us span {
  font-size: 1.8em;
  color: white;
}

footer {
  background-color: #000000;
}
footer div {
  display: flex;
  justify-content: space-between;
  padding: 40px;
}

footer img {
  width: 18%;
  height: 100px;
}

footer :last-child {
  padding-top: 0;
}

@media (max-width: 1000px) {
  .container {
    max-width: 100%;
    margin-left: 0;
    margin-right: 0;
  }
  header h1 {
    font-size: 2rem;
    padding-top: 50px;
    margin-bottom: 20px;
  }
  header h3 {
    font-size: 1rem;
  }
  .section1 {
    flex-direction: column;
  }
  .section1 p {
    max-width: 100%;
    padding: 20px 0 20px 0;
  }
  .btn {
    margin-bottom: 10px;
    font-size: 1rem;
  }
  h2 {
    font-size: 1.5rem;
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .easySchemeContainer {
    max-width: 100%;
  }
  .easySchemeContainer ol {
    font-size: 0.8rem;
    margin-right: 20px;
    margin-bottom: 20px;
  }
  .section1 {
    padding-top: 10px;
    padding-bottom: 20px;
  }
  .section1 p {
    font-size: 0.8rem;
    max-width: 100%;
    padding: 10px 0 10px 0;
  }
  .section2 {
    flex-direction: column;
  }
  .section2 ol {
    font-size: 0.8rem;
    padding-right: 20px;
    padding-bottom: 20px;
  }
  .section2 a {
    font-size: 0.7rem;
  }
  .section2 h3 {
    font-size: 1rem;
    margin-bottom: 20px;
    margin-left: 20px;
  }
  .section2 > div {
    width: 100%;
  }
  #btn_container {
    padding-right: 20px;
    justify-content: space-around;
    margin-top: 0;
  }
  #btn_container > a {
    padding-top: 0;
    padding-bottom: 0;
  }
  .contact_us {
    padding: 20px 0 20px 0;
  }
  .contact_us span {
  font-size: 1.1em;
  }
  footer div {
    display: flex;
    justify-content: space-between;
    padding: 20px;
  }
  
  footer img {
    width: 18%;
    height: 50px;
  }
}